          console.log(AddressArr);
          arr.push(obj);
          this.getlnglat(arr); // 将点击后获取的经纬度传给地图
          var geocoder = new AMap.Geocoder({
            radius: 1000,
            extensions: "all"
          });
          geocoder.getAddress(AddressArr, function(status, result) {
            if (status === "complete" && result.info === "OK") {
              if (result && result.regeocode) {
                // console.log("result.regeocode:::::::", result.regeocode.pois);
                // self.$emit("pointResult", result); //抛出鼠标点击的值
                self.address = result.regeocode.formattedAddress;
                self.poisArray = result.regeocode.pois[0];
                console.log("result", result.regeocode.pois[0]);
                self.$nextTick();
              }
            }
          });
        }
