            // });
          }
        }
      };
      return marker;
    },
    addWindow(lng, lat, index) {
      //添加消息提示信息 遮盖物点击弹出的消息
      let obj = {
        position: [lng, lat + index * 0.001],
        content: `<div class="prompt">[${lng},${lat}]</div>`,
        visible: false
      };
      return obj;
    },
    getlnglat(arr, latSum = 0, lngSum = 0) {
      this.markers = [];
      // this.windows = [];
      let self = this;
      if (arr.length > 0) {
        arr.forEach(function(item, index) {
          let { lng, lat } = item;
          lngSum += lng;
          latSum += lat;
          self.markers.push(self.addmarker(lng, lat, index));
          // self.windows.push(self.addWindow(lng, lat, index));
        });
        let center = {
          lng: lngSum / arr.length,
          lat: latSum / arr.length
        };
        this.mapCenter = [center.lng, center.lat];
      }
