      let currentIndex = index;
      if (currentIndex === index) {
        console.log(123);
      } else {
        console.log(456);
      }
      currentIndex = index;

      this.markers.push(
        this.addmarker(item.point[0], item.point[1], index - 1)
      );
      var geocoder = new AMap.Geocoder({
        radius: 1000,
        extensions: "all"
      });
      geocoder.getAddress(item.point, function(status, result) {
        if (status === "complete" && result.info === "OK") {
          if (result && result.regeocode) {
            // console.log("result.regeocode:::::::", result.regeocode.pois);
            // console.log('result',result)
            //self.$emit("pointResult", result); //抛出鼠标点击的值

            self.address = result.regeocode.formattedAddress;
            self.$nextTick();
          }
        }
      });
      // if (!this.markers.length) return;
      //  this.markers.splice(this.markers.length - 1, 1);
      // console.log(item.point,index)
      // this.getlnglat(item.point);
    },
  // historyClick(point){
    //   let arr = [];
    //   let obj = { lng: point[0], lat: point[1] };
    //   arr.push(obj);
    //   console.log(arr)
    //   this.getlnglat(arr);
    //   // console.log('point',point)
    //   // let pointRes = {}
    //   // this.getlnglat({});
    // },


    confirmClick() {
      console.log(this.poisArray);
      let param = {
        latitude:this.poisArray.location.lat,
        longitude:this.poisArray.location.lng,
        name:`${this.poisArray.address}${this.poisArray.name}`
      }
      this.$emit('map-message',param)
  }
  }
};
</script>
