import Vue from 'vue'
import App from './App.vue'
import VueAMap  from 'vue-amap'

Vue.config.productionTip = false

Vue.use(VueAMap);

VueAMap.initAMapApiLoader({ //高德地图初始化
  key: '82af10f68406a3b4e43bab85b9f07042', //自己申请的key
  plugin: [
    'AMap.PlaceSearch',// POI搜索插件
    'AMap.Scale', // 右下角缩略图插件 比例尺
    'AMap.OverView', // 地图鹰眼插件
    'AMap.MapType', // 类别切换控件，实现默认图层与卫星图、实施交通图层之间切换的控制
    'AMap.PolyEditor', // 编辑 折线多，边形
    'AMap.CircleEditor',// 圆形编辑器插件
    'AMap.Geolocation',
    'AMap.Geocoder',// 地理编码与逆地理编码服务，用于地址描述与坐标间的相互转换
    'AMap.CitySearch'
  ],
  v: '1.4.4'
})// 使用此插件时 需传一个初始化定位坐标 如：<gadMapArea :lngitude="108.877002" :latitude="34.192676" @lnglatHandler="lnglatHandler"></gadMapArea>

new Vue({
  render: h => h(App),
}).$mount('#app')
