<template>
  <div id="app">
    <!-- <v-amap :lngitude="lngitude" :latitude="latitude"></v-amap> -->
    <!-- <Example></Example> -->
    <vAmap :lngitude="longitude" :latitude="latitude" :showSearchBox='showSearchBox'  @pointResult='pointResult' :showOverImg='showOverImg'></vAmap>
  </div>
</template>

<script>

// import vAmap from './components/Amap.vue'
//import Example from './Example.vue'
import vAmap from './components/Amap.vue'
const ipcRenderer = window.require('electron').ipcRenderer
export default {
  name: 'app',
  data() {
    return {
      longitude: '116.397128',
      latitude: '39.916527',
      showSearchBox:true,
      showOverImg:true,
    }
  },
  components: {
     vAmap,
    //Example
  },
  created() {
    let searchParams = new URLSearchParams(document.location.search)
    console.log('searchParams',searchParams)
    console.log('document.location.search:::', document.location)
    if (!document.location.search) return;
    if(this.longitude || this.latitude){
      this.longitude = searchParams.get("longitude")
      this.latitude = searchParams.get("latitude")
    }
    this.showSearchBox =searchParams.get("isSearch")==='true'
    if(this.showSearchBox == true){
      this.showOverImg = true
    }else{
      this.showOverImg = false
    }

    console.log(this.longitude, this.latitude,this.showSearchBox)
  },
  methods:{
    pointResult(val){//抛出的值
      if (!val||val.length===0) return
      console.log('接收到结果',val)
      ipcRenderer.send('send-map-message', val[0])
    }

  }
}
</script>

<style>
body,
html,
#app {
  width: 100%;
  height: 100%;
  font-family: '微软雅黑', 'Microsoft YaHei', Arial, Helvetica, sans-serif, '宋体';
  -webkit-font-smoothing: antialiased;
  overflow: hidden;
  margin: 0px;
}
</style>
